import React from 'react';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts';

const PriceChart = ({ t, datasets }) => {
  // Color palette matching category analysis
  const colors = [
    { count: '#624E88', avgPrice: '#8967B3' },  // Blue for count, red for avg price
    { count: '#386641', avgPrice: '#6a994e' }   // Green for count, orange for avg price
  ];

  // Normalize and calculate weighted metrics
  const processedDatasets = datasets.map((dataset, index) => {
    // Calculate total sales and max values for normalization
    const maxAvgPrice = Math.max(...dataset.data.map(item => item.avgPrice));
    const maxCount = Math.max(...dataset.data.map(item => item.count));

    // Normalize and create weighted metric
    const normalizedData = dataset.data.map(item => {
      // Sales volume weight (normalized)
      const salesVolumeWeight = item.count / maxCount;
      
      // Price weight (normalized)
      const priceWeight = item.avgPrice
      const priceWeightE = item.avgPrice / maxAvgPrice;
      
      // Relevance metric: 0-100 scale
      // Combines price and sales volume with equal weight
      const relevance = Math.round((salesVolumeWeight + priceWeightE) * 50);

      return {
        ...item,
        avgPriceNormalized: priceWeight,
        relevance
      };
    });

    return {
      ...dataset,
      data: normalizedData,
      color: colors[index]
    };
  });

  return (
    <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow-sm">
      <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
        {t('price_trend')}
      </h3>
      <div className="h-80">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart
            margin={{
              top: 5,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="date"
              type="category"
              allowDuplicatedCategory={false}
              tickFormatter={(date) => new Date(date).toLocaleDateString('ja-JP')}
            />
            <YAxis 
              yAxisId="left"
              label={{ value: 'Relevance', angle: -90, position: 'insideLeft' }}
              domain={[0, 100]}
            />
            <YAxis 
              yAxisId="right" 
              orientation="right"
              label={{ value: 'Average Price', angle: 90, position: 'insideRight' }}
            />
            <Tooltip
              cursor={{ stroke: 'rgba(0,0,0,0.2)', strokeWidth: 2 }}
              content={({ active, payload, label }) => {
                if (active && payload && payload.length) {
                  return (
                    <div className="bg-white dark:bg-gray-800 p-4 border rounded shadow-lg">
                      <p className="font-bold mb-2">{new Date(label).toLocaleDateString('ja-JP')}</p>
                      {payload.map((entry, index) => (
                        <div key={`item-${index}`} className="flex items-center">
                          <div 
                            className="w-4 h-4 mr-2" 
                            style={{ backgroundColor: entry.stroke }}
                          />
                          <p>
                            {entry.name}: {entry.name.includes('Average Price') 
                              ? `${entry.payload.avgPrice.toLocaleString()}` 
                              : entry.value}
                          </p>
                        </div>
                      ))}
                    </div>
                  );
                }
                return null;
              }}
            />
            <Legend />
            
            {processedDatasets.flatMap((dataset, index) => [
              // Average Price Line
              <Line
                key={`price_${dataset.idol}`}
                yAxisId="right"
                type="monotone"
                dataKey="avgPriceNormalized"
                data={dataset.data}
                stroke={dataset.color.avgPrice}
                strokeWidth={2}
                activeDot={{ 
                  r: 8,
                  strokeWidth: 2,
                  stroke: dataset.color.avgPrice
                }}
                name={`${t(`idols.${dataset.idol}`)} - Average Price`}
              />,
              // Relevance Line
              <Line
                key={`relevance_${dataset.idol}`}
                yAxisId="left"
                type="monotone"
                dataKey="relevance"
                data={dataset.data}
                stroke={dataset.color.count}
                strokeWidth={2}
                activeDot={{ 
                  r: 8,
                  strokeWidth: 2,
                  stroke: dataset.color.count
                }}
                name={`${t(`idols.${dataset.idol}`)} - Relevance`}
              />
            ])}
          </LineChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
};

export default PriceChart;