import Papa from 'papaparse';

// Function to load local file content
export const loadLocalFile = async (filename) => {
  try {
    const response = await fetch(filename);
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    return await response.text();
  } catch (error) {
    console.error(`Error reading file ${filename}:`, error);
    throw error;
  }
};

export const loadAuctionData = async (filename) => {
  try {
    const response = await loadLocalFile(filename);
    return new Promise((resolve, reject) => {
      Papa.parse(response, {
        header: true,
        dynamicTyping: true,
        skipEmptyLines: true,
        complete: (results) => {
          // Validate required columns
          const requiredColumns = ['date', 'title', 'successful_bid'];
          const missingColumns = requiredColumns.filter(col => 
            !results.meta.fields.includes(col)
          );

          if (missingColumns.length > 0) {
            reject(new Error(`Missing columns: ${missingColumns.join(', ')}`));
            return;
          }

          resolve(results.data);
        },
        error: (error) => reject(error)
      });
    });
  } catch (error) {
    console.error('Error parsing auction data:', error);
    throw error;
  }
};

export const loadTrackingData = async (filename) => {
  try {
    const response = await loadLocalFile(filename);
    const parsedData = JSON.parse(response);

    // Return a default object if tracking data is incomplete
    return {
      last_processed_date: new Date().toISOString(),
      ...parsedData
    };
  } catch (error) {
    console.error('Error parsing tracking data:', error);
    
    // Return a fallback tracking data object
    return {
      last_processed_date: new Date().toISOString(),
      last_page: 0
    };
  }
};

export const categorizeItem = (title) => {
  const lowerTitle = title.toLowerCase();
  
  // More comprehensive categorization
  const categories = [
    { key: '', name: '' },
    { key: '', name: '' },
    { key: '', name: '' },
    { key: '', name: '' },
    { key: '', name: '' }
  ];

  // Find the first matching category
  const matchedCategory = categories.find(cat => 
    lowerTitle.includes(cat.key)
  );

  return matchedCategory ? matchedCategory.name : '';
};