import React from 'react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts';

const CategoryBreakdown = ({ t, datasets }) => {
  // Color palette matching price chart
  const colors = [
    { count: '#624E88', avgPrice: '#8967B3' },  // Blue for count, red for avg price
    { count: '#386641', avgPrice: '#6a994e' }   // Green for count, orange for avg price
  ];

  // Combine datasets while preserving order
  const mergedData = (() => {
    // Collect all unique categories while preserving dataset order
    const allCategories = new Set();
    datasets.forEach(dataset => {
      dataset.forEach(item => allCategories.add(item.category));
    });

    // Create merged dataset
    return Array.from(allCategories).map(category => {
      const mergedItem = { category };
      
      datasets.forEach((dataset, index) => {
        const matchingCategory = dataset.find(item => item.category === category);
        
        if (matchingCategory) {
          mergedItem[`count_${index}`] = matchingCategory.count;
          mergedItem[`avgPrice_${index}`] = matchingCategory.avgPrice;
        } else {
          // Maintain consistent ordering with 0 values
          mergedItem[`count_${index}`] = 0;
          mergedItem[`avgPrice_${index}`] = 0;
        }
      });

      return mergedItem;
    });
  })();

  return (
    <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow-sm">
      <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-4">
        {t('category_analysis')}
      </h3>
      <div className="h-80">
        <ResponsiveContainer width="100%" height="100%">
          <BarChart
            data={mergedData}
            margin={{
              top: 5,
              right: 30,
              left: 20,
              bottom: 5,
            }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis
              dataKey="category"
              tickFormatter={(category) =>
                t(`category_default.${category}`, category)
              }
            />
            <YAxis yAxisId="left" />
            <YAxis yAxisId="right" orientation="right" />
            <Tooltip
              formatter={(value, name, props) => {
                const [nameBase, type, idolIndex] = name.split(' - ');
                return [
                  value.toLocaleString(),
                  `${nameBase} - ${type}`
                ];
              }}
              labelFormatter={(category) =>
                t(`category_default.${category}`, category)
              }
            />
            <Legend />
            
            {datasets.flatMap((dataset, index) => [
              <Bar
                key={`count_${dataset[0]?.idol || index}`}
                yAxisId="left"
                dataKey={`count_${index}`}
                fill={colors[index].count}
                name={`${t(`idols.${dataset[0]?.idol || 'unknown'}`)} - ${t('chart_labels.transaction_count')}`}
              />,
              <Bar
                key={`avgPrice_${dataset[0]?.idol || index}`}
                yAxisId="right"
                dataKey={`avgPrice_${index}`}
                fill={colors[index].avgPrice}
                name={`${t(`idols.${dataset[0]?.idol || 'unknown'}`)} - ${t('chart_labels.average_price')}`}
              />
            ])}
          </BarChart>
        </ResponsiveContainer>
      </div>
    </div>
  );
};

export default CategoryBreakdown;