import React, { useState, useEffect } from 'react';
import { Sun, Moon, Globe } from 'lucide-react';
import _ from 'lodash';
import Stats from './components/Stats';
import PriceChart from './components/PriceChart';
import CategoryBreakdown from './components/CategoryBreakdown';
import Filters from './components/Filters';
import { loadAuctionData, loadTrackingData, categorizeItem } from './utils/data';
import { useTranslation } from './utils/translations';

function App() {
  // Dark mode and language state
  const [darkMode, setDarkMode] = useState(() => {
    const savedMode = localStorage.getItem('darkMode');
    if (savedMode !== null) {
      return savedMode === 'true';
    }
    return window.matchMedia('(prefers-color-scheme: dark)').matches;
  });
  
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [language, setLanguage] = useState(localStorage.getItem('appLanguage') || 'ja');
  const t = useTranslation(language);
  
  const [timeFrame, setTimeFrame] = useState('all');
  const [aggregationType, setAggregationType] = useState('daily');
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [lastUpdate, setLastUpdate] = useState(null);
  
  const [csvFiles, setCsvFiles] = useState([
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv',
    'yahoo_auction_.csv'
  ]);

  const [selectedFile, setSelectedFile] = useState('');
  const [secondaryFile, setSecondaryFile] = useState('');
  const [secondaryData, setSecondaryData] = useState([]);

  // Load primary data when selected file changes
  useEffect(() => {
    const init = async () => {
      if (!selectedFile) return;

      try {
        setLoading(true);
        const trackingFilePath = selectedFile.replace('.csv', '.json').replace('yahoo_auction_', 'tracking_');

        const auctionData = await loadAuctionData(selectedFile);
        const processedData = auctionData.map(item => ({
          ...item,
          date: new Date(item.date),
          category: categorizeItem(item.title)
        }));

        const tracking = await loadTrackingData(trackingFilePath);
        setLastUpdate(tracking.last_processed_date);
        
        setData(processedData);
        setError(null);
      } catch (err) {
        console.error('Error loading data:', err);
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };

    init();
  }, [selectedFile]);

  // Load secondary data when secondary file changes
  useEffect(() => {
    const loadSecondaryData = async () => {
      if (!secondaryFile) {
        setSecondaryData([]);
        return;
      }

      try {
        setLoading(true);
        const trackingFilePath = secondaryFile.replace('.csv', '.json').replace('yahoo_auction_', 'tracking_');

        const auctionData = await loadAuctionData(secondaryFile);
        const processedData = auctionData.map(item => ({
          ...item,
          date: new Date(item.date),
          category: categorizeItem(item.title)
        }));

        setSecondaryData(processedData);
      } catch (err) {
        console.error('Error loading secondary data:', err);
      } finally {
        setLoading(false);
      }
    };

    loadSecondaryData();
  }, [secondaryFile]);

  // Set initial selected file
  useEffect(() => {
    if (csvFiles.length > 0) {
      setSelectedFile(csvFiles[0]);
    }
  }, [csvFiles]);

  // Filter function for time periods
  const filterByTimeFrame = (item) => {
    if (timeFrame === 'all') return true;

    const now = new Date();
    const itemDate = item.date;
    const timeDiff = now - itemDate;

    switch (timeFrame) {
      case 'day':
        return timeDiff <= 24 * 60 * 60 * 1000;
      case 'week':
        return timeDiff <= 7 * 24 * 60 * 60 * 1000;
      case 'month':
        return timeDiff <= 30 * 24 * 60 * 60 * 1000;
      default:
        return true;
    }
  };

  // Apply filters to main data
  const filteredData = data.filter(item => {
    if (selectedCategories.length > 0 && !selectedCategories.includes(item.category)) {
      return false;
    }
    return filterByTimeFrame(item);
  });

  // Apply filters to secondary data
  const filteredSecondaryData = secondaryData.filter(item => {
    if (selectedCategories.length > 0 && !selectedCategories.includes(item.category)) {
      return false;
    }
    return filterByTimeFrame(item);
  });

  // Prepare chart data with aggregation
  const prepareChartData = (data, aggregationType) => {
    let groupedData = _.chain(data);

    switch (aggregationType) {
      case 'weekly':
        groupedData = groupedData.groupBy(item => {
          const date = new Date(item.date);
          const startOfWeek = new Date(date);
          startOfWeek.setDate(date.getDate() - date.getDay());
          return startOfWeek.toISOString().split('T')[0];
        });
        break;
      case 'monthly':
        groupedData = groupedData.groupBy(item => {
          const date = new Date(item.date);
          return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-01`;
        });
        break;
      default: // daily
        groupedData = groupedData.groupBy(item => 
          item.date.toISOString().split('T')[0]
        );
    }

    return groupedData
      .map((items, date) => ({
        date,
        avgPrice: _.meanBy(items, 'successful_bid'),
        count: items.length
      }))
      .orderBy('date')
      .value();
  };

  // Prepare datasets for chart
  const chartDatasets = [
    {
      idol: selectedFile.replace('yahoo_auction_', '').replace('.csv', ''),
      data: prepareChartData(filteredData, timeFrame === 'all' ? aggregationType : 'daily')
    }
  ];

  if (secondaryFile) {
    chartDatasets.push({
      idol: secondaryFile.replace('yahoo_auction_', '').replace('.csv', ''),
      data: prepareChartData(filteredSecondaryData, timeFrame === 'all' ? aggregationType : 'daily')
    });
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading...</p>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p className="text-red-500">Error: {error}</p>
      </div>
    );
  }

  return (
    <div className={darkMode ? 'dark' : 'light'}>
      <div className="min-h-screen bg-white dark:bg-gray-900 text-gray-900 dark:text-white transition-colors duration-300 flex flex-col">
        <div className="flex-grow max-w-7xl mx-auto px-4 py-8 w-full">
          {/* Header */}
          <div className="flex justify-between items-center mb-8">
            <div className="w-full">
              <div className="flex items-center justify-between">
              <h1 className="text-2xl font-semibold text-gray-900 dark:text-white inline">
                  {t('auction_analysis')}
                  <a href="https://x.com/oonomoco" className="text-sm text-gray-600 dark:text-gray-400 ml-2 hover:underline">
                    by Kanon
                  </a>
                </h1>
                <div className="flex items-center space-x-2">
                  <button
                    onClick={() => setLanguage(lang => lang === 'ja' ? 'en' : 'ja')}
                    className="p-2 rounded-full hover:bg-gray-200 dark:hover:bg-gray-700"
                    aria-label={t('language.toggle')}
                  >
                    <Globe className="w-5 h-5" />
                  </button>
                  <button
                    onClick={() => setDarkMode(!darkMode)}
                    className="p-2 rounded-full hover:bg-gray-200 dark:hover:bg-gray-700"
                    aria-label={t('dark_mode.toggle')}
                  >
                    {darkMode ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
                  </button>
                </div>
              </div>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Insights and Analytics Dashboard for 46 Yahoo! Auction Sales Trends.
              </p>

              {/* File Selection */}
              <div className="mt-4 space-y-2">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                      1st
                    </label>
                    <select 
                      value={selectedFile} 
                      onChange={(e) => setSelectedFile(e.target.value)}
                      className="block w-full rounded-md border-gray-300 dark:border-gray-600 dark:bg-gray-800 dark:text-white"
                    >
                      {csvFiles.map(file => (
                        <option key={file} value={file}>
                          {t(`idols.${file.replace('yahoo_auction_', '').replace('.csv', '')}`)}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                      2nd
                    </label>
                    <select 
                      value={secondaryFile} 
                      onChange={(e) => setSecondaryFile(e.target.value)}
                      className="block w-full rounded-md border-gray-300 dark:border-gray-600 dark:bg-gray-800 dark:text-white"
                    >
                      <option value=""></option>
                      {csvFiles
                        .filter(file => file !== selectedFile)
                        .map(file => (
                          <option key={file} value={file}>
                            {t(`idols.${file.replace('yahoo_auction_', '').replace('.csv', '')}`)}
                          </option>
                        ))}
                    </select>
                  </div>
                </div>
                
                {lastUpdate && lastUpdate !== new Date().toISOString() && (
                  <p className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                    {t('last_updated')}: {new Date(lastUpdate).toLocaleString(language)}
                  </p>
                )}
              </div>
            </div>
          </div>

          {/* Filters */}
          <Filters
            t={t}
            categories={Array.from(new Set(data.map(item => item.category)))}
            selectedCategories={selectedCategories}
            onCategoryChange={(category) => {
              setSelectedCategories(prev => 
                prev.includes(category)
                  ? prev.filter(c => c !== category)
                  : [...prev, category]
              );
            }}
            timeFrame={timeFrame}
            onTimeFrameChange={setTimeFrame}
            onReset={() => {
              setSelectedCategories([]);
              setTimeFrame('all');
              setAggregationType('daily');
            }}
          />


                {/* Aggregation Type selector - only shows when timeFrame is 'all' */}
                {timeFrame === 'all' && (
                  <div className="flex items-center space-x-2 mt-2">
                    <span className="text-sm text-gray-600 dark:text-gray-400">
                      {t('aggregation_type')}:
                    </span>
                    <div className="flex space-x-2">
                      {['daily', 'weekly', 'monthly'].map((type) => (
                        <button
                          key={type}
                          onClick={() => setAggregationType(type)}
                          className={`
                            px-3 py-1 rounded-full text-sm 
                            ${aggregationType === type 
                              ? 'bg-green-500 text-white' 
                              : 'bg-gray-100 text-gray-700 hover:bg-gray-200 dark:bg-gray-700 dark:text-gray-300 dark:hover:bg-gray-600'
                            }
                          `}
                        >
                          {t(`aggregation_options.${type}`)}
                        </button>
                      ))}
                    </div>
                  </div>
                )}
          {/* Stats */}
          <div className="mb-8">
            <Stats 
              t={t} 
              datasets={chartDatasets}
            />
          </div>

          {/* Charts */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            <PriceChart 
              t={t} 
              datasets={chartDatasets}
              aggregationType={timeFrame === 'all' ? aggregationType : 'daily'}
            />
<CategoryBreakdown 
  t={t} 
  datasets={chartDatasets.map(dataset => {
    const rawData = dataset.idol === selectedFile.replace('yahoo_auction_', '').replace('.csv', '') 
      ? filteredData 
      : filteredSecondaryData;

    return _.chain(rawData)
      .groupBy('category')
      .map((items, category) => ({
        category,
        count: items.length,
        avgPrice: _.meanBy(items, 'successful_bid'),
        idol: dataset.idol
      }))
      .value();
  }).filter(dataset => dataset.length > 0)}
/>
          </div>
        </div>

        {/* Footer */}
        <footer className="bg-gray-100 dark:bg-gray-800 py-4 mt-8">
          <div className="max-w-7xl mx-auto px-4 flex justify-between items-center">
            <div className="text-sm text-gray-600 dark:text-gray-400">
               2025 Little Japanese Girls Technologies
            </div>
            <div className="space-x-4">
            <a 
                href="https://otakucanchangetheworld.netlify.app" 
                className="text-sm text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300"
              >
                Blog
              </a>
              <a 
                href="https://spergsort.netlify.app" 
                className="text-sm text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300"
              >
                Sorter
              </a>
              <a 
                href="https://chat.soma.cx/3dpd" 
                className="text-sm text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300"
              >
                Chat
              </a>
            </div>
          </div>
        </footer>
      </div>
    </div>
  );
}

export default App;