import React from 'react';

const StatCard = ({ t, title, value, subtitle, isPositive }) => {
  const valueColor = isPositive === true 
    ? 'text-green-600' 
    : isPositive === false 
      ? 'text-red-600' 
      : 'text-gray-900 dark:text-white';

  return (
    <div className="bg-white dark:bg-gray-800 p-4 rounded-lg shadow-sm">
      <dt className="text-sm font-medium text-gray-500 dark:text-gray-400 truncate">
        {title}
      </dt>
      <dd className={`mt-1 text-2xl font-semibold ${valueColor}`}>
        {value}
      </dd>
      {subtitle && (
        <dd className={`mt-1 text-xs ${valueColor}`}>
          {subtitle}
        </dd>
      )}
    </div>
  );
};

const Stats = ({ t, datasets }) => {
  // Ensure datasets is an array and filter out any undefined or empty datasets
  const validDatasets = datasets.filter(dataset => 
    dataset && dataset.data && dataset.data.length > 0
  );

  // Calculate statistics for each idol
  const idolStats = validDatasets.map(dataset => {
    const data = dataset.data;
    
    // Safely calculate total sales
    const totalSales = data.reduce((sum, item) => 
      sum + ((item.avgPrice || 0) * (item.count || 0)), 0);
    
    // Safely calculate average price
    const avgPrice = data.length > 0 
      ? data.reduce((sum, item) => sum + (item.avgPrice || 0), 0) / data.length
      : 0;
    
    // Safely calculate price change
    let priceChange = 0;
    let percentageChange = 0;
    
    if (data.length > 1) {
      const sortedData = [...data].sort((a, b) => new Date(a.date) - new Date(b.date));
      const firstPeriod = sortedData[0];
      const lastPeriod = sortedData[sortedData.length - 1];
      
      priceChange = (lastPeriod.avgPrice || 0) - (firstPeriod.avgPrice || 0);
      percentageChange = firstPeriod.avgPrice 
        ? ((priceChange / firstPeriod.avgPrice) * 100).toFixed(1)
        : 0;
    }
    
    // Safely calculate total transactions
    const totalTransactions = data.reduce((sum, item) => sum + (item.count || 0), 0);

    return {
      idol: dataset.idol,
      totalSales,
      avgPrice,
      priceChange,
      percentageChange,
      totalTransactions
    };
  });

  return (
    <div className="w-full overflow-x-auto">
      <div className="flex space-x-4">
        {idolStats.map(stat => (
          <div key={stat.idol} className="flex-1 min-w-0">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
              {t(`idols.${stat.idol}`)}
            </h3>
            <div className="grid grid-cols-2 gap-2">
              {/* Total Sales */}
              <StatCard
                t={t}
                title={t('total_sales')}
                value={`${Math.round(stat.totalSales).toLocaleString()}`}
              />
              
              {/* Average Price */}
              <StatCard
                t={t}
                title={t('average_price')}
                value={`${Math.round(stat.avgPrice).toLocaleString()}`}
              />
              
              {/* Price Change */}
              <StatCard
                t={t}
                title={t('price_change')}
                value={`${Math.round(stat.priceChange).toLocaleString()}`}
                subtitle={`${stat.percentageChange}%`}
                isPositive={stat.priceChange > 0 ? true : 
                             stat.priceChange < 0 ? false : null}
              />
              
              {/* Transactions */}
              <StatCard
                t={t}
                title={t('transaction_count')}
                value={stat.totalTransactions.toLocaleString()}
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Stats;