import React from 'react';
import { Filter, X } from 'lucide-react';

const Filters = ({ 
  t,
  categories,
  selectedCategories,
  onCategoryChange,
  timeFrame,
  onTimeFrameChange,
  onReset
}) => {
  const timePeriods = [
    { id: 'all', labelKey: 'time_periods_options.all' },
    { id: 'day', labelKey: 'time_periods_options.day' },
    { id: 'week', labelKey: 'time_periods_options.week' },
    { id: 'month', labelKey: 'time_periods_options.month' }
  ];

  return (
    <div className="bg-white dark:bg-gray-800 p-4 rounded-lg shadow-sm mb-6">
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center space-x-2">
          <Filter className="w-5 h-5" />
          <h3 className="font-medium">{t('filters')}</h3>
        </div>
        {(selectedCategories.length > 0 || timeFrame !== 'all') && (
          <button
            onClick={onReset}
            className="text-sm text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-300 flex items-center space-x-1"
          >
            <X className="w-4 h-4" />
            <span>{t('reset')}</span>
          </button>
        )}
      </div>

      <div className="space-y-4">
        {/* Time Frame Filters */}
        <div>
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            {t('time_periods')}
          </label>
          <div className="flex flex-wrap gap-2">
            {timePeriods.map((option) => (
              <button
                key={option.id}
                onClick={() => onTimeFrameChange(option.id)}
                className={`px-3 py-1 rounded-full text-sm ${
                  timeFrame === option.id
                    ? 'bg-blue-100 text-blue-700 dark:bg-blue-900 dark:text-blue-100'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200 dark:bg-gray-700 dark:text-gray-300 dark:hover:bg-gray-600'
                }`}
              >
                {t(option.labelKey)}
              </button>
            ))}
          </div>
        </div>

        {/* Category Filters */}
        <div>
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            {t('categories')}
          </label>
          <div className="flex flex-wrap gap-2">
            {categories.map((category) => (
              <button
                key={category}
                onClick={() => onCategoryChange(category)}
                className={`px-3 py-1 rounded-full text-sm ${
                  selectedCategories.includes(category)
                    ? 'bg-blue-100 text-blue-700 dark:bg-blue-900 dark:text-blue-100'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200 dark:bg-gray-700 dark:text-gray-300 dark:hover:bg-gray-600'
                }`}
              >
                {t(`category_default.${category}`, category)}
              </button>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Filters;